import React from 'react';
import { Pagination } from 'react-bootstrap'

const JPagination = ({ page, setPage, hasNextPage }) => {

    const adjustPageHandler = (amount) => {
        setPage(prevPage => prevPage + amount)
    }

    return (
        <Pagination>
            {page !== 1 && <Pagination.Prev onClick={() => adjustPageHandler(-1)} />}
            {page !== 1 && <Pagination.Item onClick={() => adjustPageHandler(-page + 1)}>1</Pagination.Item>}
            {page > 2 && <Pagination.Ellipsis />}
            {page > 2 && <Pagination.Item onClick={() => adjustPageHandler(-1)}>{page - 1}</Pagination.Item>}
            <Pagination.Item active>{page}</Pagination.Item>
            {hasNextPage && <Pagination.Item onClick={() => adjustPageHandler(1)}>{page + 1}</Pagination.Item>}
            {hasNextPage && <Pagination.Next onClick={() => adjustPageHandler(1)} />}
        </Pagination>
    )
}

export default JPagination